.popup__title {
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    margin: 0 0 39px;
    color: #000000;
}

@media screen and (max-width: 560px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin: 0 0 66px;
    }
}